import { data } from "pages/news/data";
import { Item } from "./Item";
import { ArrowRight } from "pages/main/sections/faq/ArrowRight";
import { useState } from "react";

export const Layout = () => {
    const [currentPage, setCurrentPage] = useState<number>(1);
    const postPerRow = 3;

    const posts = data.slice(0, currentPage * postPerRow);
    const isAllShown = data.length === posts.length;

    const handleClick = () => {
        setCurrentPage((currentValue) => currentValue + 1);
    };

    return (
        <section className="section overflow-hidden">
            <div className="wide-container flex-wrap pb-[120px] mobile:pb-20">
                <h2 className="title mb-6">Blog</h2>
                <div className="relative w-full after:absolute after:w-[9999px] mobile:after:w-[110%] after:h-[2px] after:bottom-0 after:left-1/2 mobile:after:left-0 after:bg-[#0F85FF] mobile:pb-9">
                    <p className="max-w-[550px] mobile:max-w-none text-xl mobile:text-lg font-medium leading-normal">
                        Get loan with a chat bot is much faster and more
                        convenient.
                    </p>
                </div>
                <div
                    className="grid w-full mt-5 mobile:mt-0"
                    style={{
                        gridTemplateColumns:
                            "repeat(auto-fit, minmax(337px, 1fr))",
                        gap: "2.5rem",
                    }}
                >
                    {posts.map((post) => (
                        <Item key={post.slug} post={post} />
                    ))}
                </div>
                {!isAllShown && (
                    <button
                        type="button"
                        className="btn_outline w-[124px] mt-10 ml-auto"
                        onClick={handleClick}
                    >
                        <span>View more</span>
                        <ArrowRight />
                    </button>
                )}
            </div>
        </section>
    );
};
